<div class="home pb-24"><div class="h-16 sticky top-0 z-20 bg-white border-b border-primary-500"><div class="max-w-container mx-auto px-4 flex items-center h-full"><img class="h-10 mr-12 hidden md:block" src="@/assets/logo.png"><img class="h-10 mr-3 block md:hidden" src="@/assets/logo-mobile.png"><form class="flex-1 flex border border-gray-300 rounded-lg" @submit.prevent=""><Dropdown class="w-48 border-gray-300 border-r hidden md:block" disableBorder v-model="form.searchProvince" :list="provinces"><template #icon v-if="form.searchProvince === 'พื้นที่ใกล้ฉัน'"><Pin class="w-5"></Pin></template></Dropdown><div class="flex-1 h-10 mr-1"><input class="h-full w-full appearance-none bg-transparent px-3" type="text" v-model="form.search" placeholder="ค้นหา ชื่อ ร้านอาหาร และเครื่องดื่ม ร้านธงฟ้า ร้านค้า OTOP และสินค้าทั่วไป"></div><button class="w-16 bg-gray-100 rounded-tr-lg rounded-br-lg flex items-center justify-center hover:bg-gray-200" type="submit"><Search class="w-4 h-4 text-gray-600"></Search></button></form><button class="flex w-10 h-10 justify-center items-center ml-4 rounded lg:hidden hover:bg-gray-100" @click="isFilterShow = true"><FilterIcon class="w-6 h-6 text-primary-600"></FilterIcon></button></div></div><div class="fixed inset-0 z-20 p-8 overflow-y-auto" ref="filterElement" v-show="isFilterShow" :class="{'pointer-events-none': !isFilterShow}"><div class="fixed inset-0" style="background-color: rgba(0, 0, 0, 0.5)" @click="isFilterShow = false"></div><div class="text-right mx-auto" style="max-width: 480px;"><button class="px-6 py-1 bg-white relative z-20 rounded-full mb-2" type="button" @click="isFilterShow = false"><span class="text-xs">✕</span>&nbsp;
ปิด</button></div><Filter class="relative z-20 mx-auto" :filter="filter" style="max-width: 480px;"></Filter></div><div class="h-12 bg-primary-500"><div class="max-w-container mx-auto px-4 text-white flex items-center h-full"><div><div class="inline underline opacity-75">หน้าแรก</div><div class="inline mx-3">/</div><div class="inline font-semibold">ค้นหา</div></div></div></div><div class="max-w-container-xl mx-auto px-4 mt-6"><div class="font-bold text-xl">ผลการค้นหา {{filter.category}}
<template v-if="filter.category !== filter.subCategory">{{filter.subCategory}}</template></div><div class="mt-12 lg:grid" style="grid-template-columns: 360px 1fr; grid-gap: 2rem;"><div><Filter class="hidden lg:block" :filter="filter"></Filter></div><div class="-my-2" id="result"><Merchant :merchant="m" v-for="(m, i) in merchants" :key="`merchant-${i}`"></Merchant><button class="mt-12 w-full mx-auto rounded p-3 bg-white border border-gray-300 block" type="button" style="max-width: 360px;">ดูเพิ่มเติม</button></div></div></div></div>