<div class="filter bg-white border border-primary-300 rounded-sm px-4 py-1" v-if="filter"><div class="my-3 font-bold font-semibold">ประเภทร้านค้า</div><RadioGroup class="mb-6" v-model="filter.category" :list="categories"></RadioGroup><div class="my-3 font-bold font-semibold">จังหวัด/ใกล้ฉัน</div><Dropdown class="mb-6" v-model="filter.province" :list="provinces"><template #icon v-if="filter.province === 'พื้นที่ใกล้ฉัน'"><Pin class="w-5"></Pin></template></Dropdown><div class="my-3 font-bold font-semibold">ราคา</div><Dropdown class="mb-6" v-model="filter.priceRange" :list="priceRanges"></Dropdown><div class="my-3 font-bold font-semibold">ประเภทร้านอาหารและเครืองดื่ม</div><RadioGroup class="mb-2" v-model="filter.subCategory" :list="subCategories"></RadioGroup></div>