<div class="radio-group"><div class="radio" v-for="l in list" :key="`key-radio-${internalId}-${encodeValue(l)}`"><input class="hidden" :id="`radio-${internalId}-${encodeValue(l)}`" type="radio" :value="l" :checked="l === modelValue" @change="$emit('update:modelValue', $event.target.value)"><label class="flex cursor-pointer py-1" :for="`radio-${internalId}-${encodeValue(l)}`"><div class="w-5 flex justify-center items-center pointer-events-none"><div class="radio-circle w-4 h-4 border rounded-full flex justify-center items-center"><div class="radio-inner-circle bg-light-blue-500 rounded-full w-2 h-2"></div></div></div><div class="pl-3 flex-1 w-full block select-none" style="padding-top: 1px;">{{l}}</div></label></div></div>