<div class="merchant bg-white border border-teal-200 my-2 sm:flex sm:p-1"><img class="w-full h-64 object-cover sm:w-64" :src="merchant.coverImageId"><div class="flex-1 px-6 py-1"><div class="border-b border-gray-200 py-4"><div class="inline-block mb-2"><span class="text-xl font-bold mr-4">{{merchant.shopNameTH}}</span><span class="inline-block relative" style="top: -2px;" v-if="merchant.isOpen !== 'N/A'"><div class="px-2 leading-loose rounded-sm text-xs font-semibold text-white bg-green-500" v-if="merchant.isOpen === 'Y'">เปิดอยู่</div><div class="px-2 leading-loose rounded-sm text-xs font-semibold text-white bg-gray-400" v-if="merchant.isOpen === 'N'">ปิดแล้ว</div></span></div><p class="text-gray-400"><span>{{merchant.subcategoryName}}</span><span class="mx-3">|</span><PriceLevel :level="merchant.priceLevel"></PriceLevel><span class="mx-3">|</span><span class="whitespace-nowrap">{{merchant.addressDistrictName}} {{merchant.addressProvinceName}}</span></p></div><div class="pt-6 pb-4 text-gray-400"><span v-html="merchant.highlightText"></span><div class="mt-2" v-if="merchant.recommendedItems.length &gt; 0"><span class="text-gray-600 font-semibold">เมนูแนะนำ:</span>&nbsp;<span>{{merchant.recommendedItems.join(', ')}}</span></div><div class="flex mt-4"><FacilityBadge class="mr-1" :facility="f" v-for="(f, i) in merchant.facilities"></FacilityBadge></div></div></div></div>