<div class="dropdown block relative"><div class="absolute inset-0 pointer-events-none flex items-center" :class="{ 'rounded-sm border border-gray-300': !disableBorder }"><div class="flex justify-center items-center px-2"><slot name="icon"></slot></div><div class="flex-1 py-2"><template v-if="modelValue">{{modelValue}}</template><div class="text-gray-400" v-else>{{placeholder}}</div></div><div class="flex justify-center items-center px-2"><ChevronDown class="w-5 text-gray-300"></ChevronDown></div></div><select class="w-full h-full appearance-none opacity-0 p-2" :value="modelValue" @change="$emit('update:modelValue', $event.target.value)"><option v-for="l in filteredList" :value="l">{{l}}</option></select></div>