
import { defineComponent, computed } from 'vue';

import ChevronDown from '@/assets/icons/chevron-down.svg';

const Dropdown = defineComponent({
  components: {
    ChevronDown,
  },
  props: {
    disableBorder: {
      type: Boolean,
    },
    modelValue: {
      required: true,
    },
    list: {
      type: Array,
      required: true,
    },
    placeholder: {
      type: String,
      default: 'กรุณาเลือก',
    },
  },
  setup(props) {
    return {
      filteredList: computed(() => (props.list || []).filter((mv: any) => mv)),
    };
  },
});

export default Dropdown;
